<template>
    <div class="scroll-wrapper">
        <div class="scroll">
            <loading :show="!success"></loading>
            <mu-flexbox v-if="success" wrap="wrap" :gutter="0" justify="space-around">
                <mu-flexbox-item class="flex-item" v-for="item in pagelists" basis="40%">
                    <div class="cover">
                        <router-link :to="{name:'playListPage',params:{id:item.id,name:item.name,coverImgUrl:item.coverImgUrl}}">
                            <img :src="item.coverImgUrl"/>
                            <div class="count">
                                <mu-icon value="headset" color="white" :size="14"/>
                                {{item.playCount | formatCount}}
                            </div>
                            <div class="info">
                                <div>{{item.name}}</div>
                                <div>by:{{item.creator.nickname}}</div>
                            </div>
                        </router-link>
                    </div>
                </mu-flexbox-item>
            </mu-flexbox>
            <mu-infinite-scroll :scroller="scroller" :loading="loadData" @load="loadMore" class="loadMore"/>
        </div>
    </div>
</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                scroller: null,
                pagelists: [],
                success: false,
                loadData: false,
                limit: 10,
            }
        },
        components: {Loading},
        mounted(){
            this.fetchData();
            this.scroller = this.$el;
        },
        beforeRouteLeave(to, from, next){
            sessionStorage.setItem('scrollTop', this.$el.offsetTop);
            next();
        },
        methods: {
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/toplist', {params: {limit: this.limit}})
                .then(m => {
                    if (m.data) {
                        this.pagelists = m.data.data.playlists;
                        this.success = true;
                    }
                });
            },
            loadMore () {
                this.loadData = true;
                this.limit += 10;
                setTimeout(() => {
                    this.fetchData();
                    this.loadData = false
                }, 1000)
            }
        }
    }
</script>
<style scoped>
    .flex-item img {
        vertical-align:top;
        width:100%;
    }

    .cover {
        position:relative;
    }

    .cover .count {
        position:absolute;
        top:0;
        width:100%;
        padding:0 4px;
        box-sizing:border-box;
        text-align:right;
        color:#fff;
        background:rgba(0, 0, 0, .1);
        background:-webkit-linear-gradient(top, rgba(4, 23, 38, .2), rgba(255,255,255,0));
    }

    .cover .count .mu-icon {
        margin-right:4px;
        vertical-align:middle;
    }

    .info {
        position:absolute;
        bottom:0;
        padding:2px 4px;
        box-sizing:border-box;
        width:100%;
        background:rgba(0, 0, 0, .2);
    }

    .info > div {
        font-size:12px;
        color:#fff;
        overflow:hidden;
        white-space:nowrap;
        text-overflow:ellipsis;
    }
</style>



