<template>
    <div class="scroll-wrapper artists">
        <div class="scroll">
            <loading :show="!success"></loading>
            <mu-flexbox :gutter="0" v-for="(item,key) in artists" class="listItem" v-if="success">
                <mu-flexbox-item basis="12%" class="listIndex" :class="{active:key<3}">
                    {{key+1}}
                </mu-flexbox-item>
                <mu-flexbox-item basis="28%" class="cover">
                    <router-link :to="{name:'artistPage',params:{id:item.id,name:item.name,picUrl:item.picUrl}}">
                        <img :src="item.picUrl">
                    </router-link>
                </mu-flexbox-item>
                <mu-flexbox-item basis="64%" class="listInfo">
                    <router-link :to="{name:'artistPage',params:{id:item.id,name:item.name,picUrl:item.picUrl}}">
                        <div class="name">{{item.name}}</div>
                        <div class="total">
                        <span>
                        <mu-icon value="album" :size="16"/>
                            {{item.albumSize}}
                        </span>
                            <span>
                            <mu-icon value="library_music" :size="16"/>
                            {{item.musicSize}}
                        </span>
                        </div>
                    </router-link>
                </mu-flexbox-item>
            </mu-flexbox>
        </div>
    </div>
</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                scroller: null,
                artists: [],
                success: false,
                loadData: false,
                limit: 10,
            }
        },
        components: {Loading},
        mounted(){
            this.fetchData();
        },
        methods: {
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/hotArtist')
                .then(m => {
                    if (m.data) {
                        this.artists = m.data.data.artists;
                        this.success = true;
                    }
                });
            }
        }
    }
</script>
<style scoped>
    .flex-item img {
        vertical-align:top;
        width:100%;
    }

    .artists {
        background:#F0F4F3;
    }

    .listIndex {
        font-size:24px;
        color:#919695;
        text-align:center;
    }

    .listIndex.active {
        color:#CD3D3D
    }

    .cover {
    }

    .listItem {
        padding:3px 0;
    }

    .listInfo {
        padding-left:10px;
    }

    .listInfo .name {
        font-size:24px;
        color:#2C2F2E;
    }

    .listInfo .total {
        margin-top:4px;
        color:#8E9190;
        font-size:18px;
    }
</style>



