<template>
    <div>
        <div class="logoBar">
            <img src="../assets/logo.svg" id="logo">
        </div>
        <mu-tabs :value="activeTab" @change="handleTabChange">
            <mu-tab value="playlist" title="歌单"/>
            <mu-tab value="album" title="新碟上架"/>
            <mu-tab value="hotArtist" title="热门歌手"/>
        </mu-tabs>
        <keep-alive>
            <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                activeTab: 'playlist'
            }
        },
        beforeRouteEnter (to, from, next) {
            next(vm => {
                if (to.name) {
                    vm.activeTab = vm.$route.name
                }

            })
        },
        methods: {
            handleTabChange (val) {
                this.activeTab = val;
                this.$router.push({path: '/index/'+val})
            }
        }
    }
</script>

<style scoped>
    .logoBar {
        padding:10px;
        background:#CC3C3D;
    }

    #logo {
        width:40%
    }
</style>



