<template>
    <div class="scroll-wrapper playList">
        <div class="scroll">
            <loading :show="!success"></loading>
            <mu-flexbox v-if="success" wrap="wrap" :gutter="0" justify="space-around">
                <mu-flexbox-item class="flex-item" v-for="item in albums" basis="40%">
                    <div class="cover">
                        <router-link :to="{name:'albumPage',params:{id:item.id,name:item.name,picUrl:item.picUrl}}">
                            <img :src="item.picUrl"/>
                            <div class="info">
                                <div>{{item.name}}</div>
                                <div>by:{{item.artist.name}}</div>
                            </div>
                        </router-link>
                    </div>
                </mu-flexbox-item>
            </mu-flexbox>
            <mu-infinite-scroll :scroller="scroller" :loading="loadData" @load="loadMore" class="loadMore"/>
        </div>
    </div>

</template>
<script>
    import Loading from '../components/Loading.vue'
    export default {
        data(){
            return {
                scroller: null,
                albums: [],
                success: false,
                loadData: false,
                limit:10,
            }
        },
        components: {Loading},
        mounted(){
            this.fetchData();
            this.scroller = this.$el;
        },
        methods: {
            fetchData(){
                this.$http.get('http://119.29.196.41:5000/api/newAlbum', {params: {limit: this.limit}})
                .then(m => {
                    if (m.data) {
                        this.albums = m.data.data.albums;
                        this.success = true;
                    }
                });
            },
            loadMore () {
                this.loadData = true;
                this.limit += 10;
                setTimeout(() => {
                    this.fetchData();
                    this.loadData = false
                }, 1000)
            }
        }
    }
</script>
<style scoped>
    .playList{
        background:#16181c;
    }
    .flex-item img {
        vertical-align:top;
        width:100%;
    }

    .cover {
        position:relative;
    }

    .cover .count {
        position:absolute;
        top:0;
        width:100%;
        padding:0 4px;
        box-sizing:border-box;
        text-align:right;
        color:#fff;
        background:rgba(0, 0, 0, .1);
    }

    .cover .count .mu-icon {
        margin-right:4px;
        vertical-align:middle;
    }

    .info {
        position:absolute;
        bottom:0;
        padding:2px 4px;
        box-sizing:border-box;
        width:100%;
        background:rgba(0, 0, 0, .2);
    }

    .info > div {
        font-size:12px;
        color:#fff;
        overflow:hidden;
        white-space:nowrap;
        text-overflow:ellipsis;
    }
</style>



